import test from 'ava';
import { str } from '../src';

test('it should captialize all words in a string', t => {
	t.is(str('foo bar baz!').capitalize(), 'Foo Bar Baz!');
	t.is(str('lorem ipsum dolor sit amet').capitalize(), 'Lorem Ipsum Dolor Sit Amet');
});

test('it should captialize the first letter in a string', t => {
	t.is(str('foo bar baz!').ucfirst(), 'Foo bar baz!');
	t.is(str('foobarbaz').ucfirst(), 'Foobarbaz');
});
